<template>
    <div class="tile-styles">
        <div @click="setTileStyle(false)" class="tile-styles__styleA" :class="{ 'tile-styles__style-active': !value }">
            <span></span>
            <span></span>
            <span></span>
        </div>
        <div @click="setTileStyle(true)" class="tile-styles__styleB" :class="{ 'tile-styles__style-active': value }">
            <span></span>
            <span></span>
            <span></span>
            <span></span>
        </div>
    </div>
</template>

<script>
  export default {
    name: "SjTileStyle",
    props: {
      value: {
        type: Boolean,
        default: false,
      },
    },
    methods: {
      setTileStyle(value) {
        this.$emit('update:value', value);
      }
    }
  }
</script>

